.common-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover{color:#8c43ce}.common-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header{margin-bottom:40px;text-align:center}.common-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.scholar-container{width:calc(33.33% - 16px);min-width:300px;flex-grow:1}@media(max-width: 1200px){.scholar-container{width:calc(50% - 12px)}}@media(max-width: 768px){.scholar-container{width:100%}}.scholar-card{position:relative;background-color:#fff;border-radius:12px;box-shadow:0 4px 16px rgba(0,0,0,.1);padding-top:20px;padding-left:20px;padding-right:20px;padding-bottom:0px;height:100%;transition:all .3s ease;overflow:hidden}.scholar-card:hover{transform:translateY(-4px);box-shadow:0 8px 24px rgba(0,0,0,.15)}.scholar-title{font-size:1.25rem;font-weight:700;margin:0 0 12px 0;color:#000;line-height:1.4;word-wrap:break-word;white-space:normal}.scholar-authors{font-size:.95rem;margin-bottom:12px;color:#a5a3a7}.scholar-conference{font-size:.9rem;margin-bottom:16px;color:#868e96}.scholar-award{font-size:.9rem;margin-bottom:16px;color:#000;border-radius:6px}.scholar-links{display:flex;flex-wrap:wrap;gap:8px;margin-top:16px}.link-button{padding:6px 12px;border-radius:6px;border:none;font-size:.85rem;font-weight:500;cursor:pointer;transition:all .2s ease;background-color:#a5a3a7;color:#fff}.link-button:hover{transform:translateY(-2px);box-shadow:0 4px 12px rgba(0,0,0,.2)}.dark-mode .scholar-card{background-color:#171c28;box-shadow:0 0 15px rgba(255,255,255,.3)}.dark-mode .scholar-card:hover{transform:translateY(-5px);box-shadow:0 5px 20px rgba(255,255,255,.5)}.dark-mode .scholar-title{color:#fff}.dark-mode .scholar-award{color:#fff}.go-corner{display:flex;align-items:center;justify-content:center;position:absolute;width:32px;height:32px;overflow:hidden;top:0;right:0;background-color:#a5a3a7;border-radius:0 4px 0 32px}.go-arrow{margin-top:-4px;margin-right:-4px;color:#fff;font-family:courier,sans}.scholar-card::before{content:"";position:absolute;z-index:-1;top:-16px;right:-16px;background:#a5a3a7;height:32px;width:32px;border-radius:32px;transform:scale(1);transform-origin:50% 50%;transition:transform .4s ease-out}.scholar-card:hover::before{transform:scale(300)}.scholar-card:hover .scholar-authors,.scholar-card:hover .scholar-conference,.scholar-card:hover .scholar-award{transition:all .3s ease-out;color:rgba(255,255,255,.8)}.scholar-card:hover .link-button{background-color:#fff;color:#a5a3a7}.scholar-card:hover .scholar-title{transition:all .3s ease-out;color:#fff}@media(max-width: 768px){.scholar-card{padding:20px}.scholar-title{font-size:1.1rem}.year-title{font-size:1.3rem}}@media(max-width: 1024px){.scholar-container{width:calc(50% - 10px)}}@media(max-width: 768px){.scholar-container{width:100%}}