.common-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover{color:#8c43ce}.common-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header{margin-bottom:40px;text-align:center}.common-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.project-container{width:calc(33% - 16px);min-width:300px;flex-grow:1}@media(max-width: 1200px){.project-container{width:calc(50% - 12px)}}@media(max-width: 768px){.project-container{width:100%}}.project-card{position:relative;background-color:#fff;border-radius:12px;box-shadow:0 4px 16px rgba(0,0,0,.1);padding:20px;height:100%;transition:all .3s ease;overflow:hidden}.project-card:hover{transform:translateY(-4px);box-shadow:0 8px 24px rgba(0,0,0,.15)}.github-star-count{color:#ff1818}.project-card-title{font-size:1.25rem;font-weight:700;margin:0 0 12px 0;color:#000;line-height:1.4;word-wrap:break-word;white-space:normal}.dark-mode .project-card{background-color:#171c28;box-shadow:0 0 15px rgba(255,255,255,.3)}.dark-mode .project-card:hover{transform:translateY(-5px);box-shadow:0 5px 20px rgba(255,255,255,.5)}.dark-mode .project-card-title,.dark-mode .project-card-subtitle{color:#fff}.go-corner{display:flex;align-items:center;justify-content:center;position:absolute;width:32px;height:32px;overflow:hidden;top:0;right:0;background-color:#a5a3a7;border-radius:0 4px 0 32px}.go-arrow{margin-top:-4px;margin-right:-4px;color:#fff;font-family:courier,sans}.project-card::before{content:"";position:absolute;z-index:-1;top:-16px;right:-16px;background:#a5a3a7;height:32px;width:32px;border-radius:32px;transform:scale(1);transform-origin:50% 50%;transition:transform .3s ease-out}.project-card:hover::before{transform:scale(100)}.project-card:hover .project-card-title,.project-card:hover .project-card-subtitle{transition:all .3s ease-out;color:rgba(255,255,255,.8)}.project-card:hover .github-star-count{transition:all .3s ease-out;color:#8c43ce}.project-image-div{display:flex;justify-content:center}@media(max-width: 768px){.project-card{padding:20px}}@media(max-width: 1024px){.project-container{width:calc(50% - 10px)}}@media(max-width: 768px){.project-container{width:100%}}