.common-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover{color:#8c43ce}.common-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header{margin-bottom:40px;text-align:center}.common-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.direction-main-div{display:flex;align-items:center;justify-content:space-between;width:100%;max-width:1200px;margin:auto;padding:80px 0;gap:100px}.direction-image-div{flex:1;max-width:50%}.direction-image{max-width:100%;height:auto;padding:10px;border-radius:12px;box-shadow:0 4px 16px rgba(0,0,0,.1)}.direction-image:hover{transform:translateY(-4px);box-shadow:0 8px 24px rgba(0,0,0,.15)}.direction-text-div{flex:1;max-width:50%}.direction-heading{font-size:2.5rem;font-weight:700;margin-bottom:50px;color:#000}.direction-text-subtitle{font-size:1.25rem;font-weight:500;color:#868e96;margin-bottom:30px}.direction-description{font-size:1rem;line-height:1.6;color:#000}.direction-text{margin-bottom:20px}.dark-mode .direction-heading{color:#fff}.dark-mode .direction-text-subtitle{color:#fff}.dark-mode .direction-text{color:#fff}.dark-mode .direction-image{background-color:#171c28;box-shadow:0 4px 16px rgba(0,0,0,.3)}@media(max-width: 768px){.direction-main-div{flex-direction:column;padding:20px 10px;margin:0 auto;margin-left:auto;margin-right:auto;text-align:center;width:70%;gap:50px}.direction-image-div{max-width:100%;padding-right:0;margin-bottom:30px}.direction-text-div{max-width:100%;text-align:center}.direction-heading{font-size:2rem}.direction-text-subtitle{font-size:1.1rem}.direction-description{font-size:.9rem}}