.common-title,.project-title,.year-section .year-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before,.project-title::before,.year-section .year-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover,.project-title:hover,.year-section .year-title:hover{color:#8c43ce}.common-title:hover::before,.project-title:hover::before,.year-section .year-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header,.scholar-header{margin-bottom:40px;text-align:center}.common-header .header-text,.scholar-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle,.scholar-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.scholar-main-div{width:100%}.scholar-text-div{display:flex;flex-direction:column;gap:48px;width:100%}.year-section{width:100%}.papers-container{display:flex;flex-wrap:wrap;gap:45px 24px;width:100%}.project-card-container{display:flex;flex-wrap:wrap;gap:45px 24px;width:100%}@media(max-width: 768px){.scholar-header .scholar-header-text{font-size:40px}.year-section .year-title{font-size:1.5rem}}