.common-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover{color:#8c43ce}.common-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header{margin-bottom:40px;text-align:center}.common-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.plog-card{width:33.33%;background-color:#fff;border-radius:12px;box-shadow:0 4px 16px rgba(0,0,0,.1);overflow:hidden;transition:all .3s ease;margin-bottom:20px}.plog-card:hover{transform:translateY(-4px);box-shadow:0 8px 24px rgba(0,0,0,.15)}.plog-image-div{width:100%;overflow:hidden}.plog-card-image{width:100%;height:100%;object-fit:cover}.plog-detail-div{display:flex;justify-content:center;padding:20px}.plog-card-title{font-size:1.25rem;font-weight:700;margin-bottom:10px;color:#000}.plog-card-subtitle{font-size:.95rem;color:#868e96;line-height:1.5}.dark-mode .plog-card{background-color:#171c28;box-shadow:0 4px 16px rgba(0,0,0,.3)}.dark-mode .plog-card-title{color:#fff}.dark-mode .plog-card-subtitle{color:#fff}