.common-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover{color:#8c43ce}.common-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header,.plog-header{margin-bottom:40px;text-align:center}.common-header .header-text,.plog-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle,.plog-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.plog-main-div{display:flex;justify-content:center;flex-wrap:wrap;gap:20px;width:100%;margin-top:80px;margin-bottom:100px;flex-direction:row;background-color:#fff}@media(max-width: 768px){.plog-main-div{flex-direction:column;padding:0}}.dark-mode .plog-header-text{color:#fff}.dark-mode .plog-main-div{background-color:#171c28}@media(max-width: 768px){.plog-header .plog-header-text{font-size:1.8rem}.plog-main-div .plog-card{width:100%;min-width:300px;margin-bottom:20px;padding:0}.plog-main-div .plog-card img{max-width:100%;height:auto}.plog-main-div .plog-card h5,.plog-main-div .plog-card p{font-size:14px;line-height:1.3}}