.common-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover{color:#8c43ce}.common-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header{margin-bottom:40px;text-align:center}.common-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.dark-mode{background-color:#171c28;color:#fff;transition:"0.1s"}