.common-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover{color:#8c43ce}.common-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header,.intro-header{margin-bottom:40px;text-align:center}.common-header .header-text,.intro-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle,.intro-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.intro-main-div{max-width:800px;margin:0 auto;padding:50px 100px;background-color:#fff;border-radius:10px;box-shadow:0 4px 8px rgba(0,0,0,.1)}@media(max-width: 500px){.intro-main-div{padding:50px 20px}}.intro-main-div:hover{transform:translateY(-5px);box-shadow:0 5px 15px rgba(0,0,0,.3)}.intro-heading{font-size:2rem;font-weight:bold;color:#000;margin-bottom:1rem}.intro-text{font-size:1rem;line-height:1.6;color:#000}.intro-text h2{font-size:1.5rem;font-weight:bold;margin-top:1.5rem;margin-bottom:1rem;color:#000}.intro-text ol{padding-left:20px;margin-bottom:1.5rem}.intro-text ol li{margin-bottom:.8rem}.intro-text a{text-decoration:underline;transition:color .3s ease}.dark-mode .intro-main-div{background-color:#171c28;color:#fff;box-shadow:0 0 15px rgba(255,255,255,.3)}.dark-mode .intro-main-div:hover{transform:translateY(-5px);box-shadow:0 5px 20px rgba(255,255,255,.5)}.dark-mode .intro-heading,.dark-mode .intro-text h2{color:#fff}.dark-mode .intro-text a{color:#fff}