.common-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover{color:#8c43ce}.common-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header{margin-bottom:40px;text-align:center}.common-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.headroom-wrapper{top:0;left:0;width:100%;max-height:100px;z-index:1100}.dark-menu{background-color:#171c28 !important;color:#fff}.dark-menu li a,.dark-menu.logo-name{color:#fff !important}.dark-menu li a:hover,.dark-menu.logo-name:hover{background-color:#000 !important}.header{background-color:rgba(255,255,255,.8);margin:0;top:0;left:0;width:100%;min-height:80px;padding:5px 5px;z-index:1100;transition:all .3s ease}.header ul{margin:0;padding:0;list-style:none;overflow:hidden}.grey-color{color:#868e96}.header li a{font-weight:500;display:block;padding:15px 20px;color:#000;text-decoration:none}.header li a:hover,.header .menu-btn:hover{background-color:#f4f4f4}.header .logo{display:flex;align-items:center;justify-content:center;float:left;font-size:1.5em;text-decoration:none;margin-left:30px;line-height:normal}.header .logo-name{font-family:"Noteworthy";color:#000;font-weight:bold;font-variant-ligatures:no-common-ligatures;-webkit-font-variant-ligatures:no-common-ligatures;padding:0 10px}.header .menu{clear:both;max-height:0;transition:max-height .2s ease-out}.header .menu-icon{cursor:pointer;display:inline-block;float:right;padding:28px 20px;position:relative;user-select:none}.header .menu-icon .navicon{background:#333;display:block;height:2px;position:relative;transition:background .2s ease-out;width:18px}.header .menu-icon .navicon-dark{background:#fff}.header .menu-icon .navicon:before,.header .menu-icon .navicon:after{background:#333;content:"";display:block;height:100%;position:absolute;transition:all .2s ease-out;width:100%}.header .menu-icon .navicon-dark:before,.header .menu-icon .navicon-dark:after{background:#fff !important;content:"";display:block;height:100%;position:absolute;transition:all .2s ease-out;width:100%}.header .menu-icon .navicon:before{top:5px}.header .menu-icon .navicon:after{top:-5px}.header .menu-btn{display:none}.header .menu-btn:checked~.menu{max-height:486px}.header .menu-btn:checked~.menu-icon .navicon{background:rgba(0,0,0,0) !important}.header .menu-btn:checked~.menu-icon .navicon:before{transform:rotate(-45deg)}.header .menu-btn:checked~.menu-icon .navicon:after{transform:rotate(45deg)}.header .menu-btn:checked~.menu-icon:not(.steps) .navicon:before,.header .menu-btn:checked~.menu-icon:not(.steps) .navicon:after{top:0}.header .header-button{display:flex;gap:20px;margin-right:30px}@media(min-width: 48em){.header li{float:left}.header{display:flex;align-items:center;justify-content:space-between}.header .menu{max-height:none}.header .menu-icon{display:none}}@media(max-width: 960px)and (min-width: 768px){.header{font-size:12px}}