// 公共标题样式
.common-title {
  font-size: 24px;
  font-weight: 700;
  color: $titleColor;
  margin-bottom: 1.5rem;
  text-align: left;
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;

  &::before {
    content: "★";
    font-size: 1.2rem;
    color: $buttonColor;
    transition: color 0.3s ease, transform 0.3s ease;
  }

  &:hover {
    color: $buttonHover;

    &::before {
      color: $buttonHover;
      transform: rotate(360deg);
    }
  }
}

.common-header {
  margin-bottom: 40px;
  text-align: center;

  .header-text {
    font-size: 56px;
    font-weight: 400;
    margin-bottom: 16px;
    color: $titleColor;
  }

  .header-subtitle {
    text-transform: uppercase;
    color: $subTitle;
    font-size: 1.2rem;
  }
}