.common-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover{color:#8c43ce}.common-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header{margin-bottom:40px;text-align:center}.common-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.switch{position:relative;display:inline-block;width:50px;height:26px}input{transform:scale(0.5)}.slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#ccc;-webkit-transition:.4s;transition:.4s}.slider::before{position:absolute;content:"";height:26px;width:28px;left:0;bottom:0;background-color:#fff;-webkit-transition:.4s;transition:.4s}input:checked+.slider{background-color:#2196f3}input:focus+.slider{box-shadow:#2196f3}input:checked+.slider::before,input:checked+.slider>.emoji{-webkit-transform:translateX(26px);-ms-transform:translateX(26px);transform:translateX(26px)}.slider.round{border-radius:34px;display:flex;align-items:center}.slider.round::before{border-radius:50%;display:grid;place-items:center}.slider::after{display:inline;position:absolute;right:9.3%;bottom:17.5%}input:checked+.slider::after{display:inline;position:absolute;right:56.3%;bottom:14.5%}.emoji{z-index:10;display:flex;align-items:center;margin-left:2px}@media all and (max-width: 786px)and (min-width: 425px){input:checked+.slider::after{right:60.3%;bottom:20.5%}.slider::after{right:15.3%;bottom:22.5%}}@media all and (max-width: 2560px)and (min-width: 1552px){.slider::after{bottom:12.5% !important}}@media all and (max-width: 1552px)and (min-width: 1440px){.slider::after{bottom:7.5% !important}}