.common-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover{color:#8c43ce}.common-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header{margin-bottom:40px;text-align:center}.common-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}#topButton{visibility:hidden;position:fixed;bottom:20px;right:30px;z-index:99;border:none;outline:none;background-color:#a5a3a7;color:#fff;cursor:pointer;padding:15px;border-radius:15px;font-size:25px}#topButton:hover{background-color:#000;transition:all ease-in-out .2s}