.common-title,.group-category-title{font-size:24px;font-weight:700;color:#000;margin-bottom:1.5rem;text-align:left;position:relative;display:inline-flex;align-items:center;gap:10px;transition:color .3s ease}.common-title::before,.group-category-title::before{content:"★";font-size:1.2rem;color:#a5a3a7;transition:color .3s ease,transform .3s ease}.common-title:hover,.group-category-title:hover{color:#8c43ce}.common-title:hover::before,.group-category-title:hover::before{color:#8c43ce;transform:rotate(360deg)}.common-header,.group-header{margin-bottom:40px;text-align:center}.common-header .header-text,.group-header .header-text{font-size:56px;font-weight:400;margin-bottom:16px;color:#000}.common-header .header-subtitle,.group-header .header-subtitle{text-transform:uppercase;color:#868e96;font-size:1.2rem}.group-main-div{width:90%;margin:0px auto;margin-top:2rem}.group-cards-div{display:flex;flex-wrap:wrap;justify-content:center;gap:20px;margin-bottom:3rem}.group-card{background-color:#fff;border-radius:10px;box-shadow:0 0 10px rgba(0,0,0,.1);padding:20px;width:calc(20% - 20px);width:calc(20% - 20px);text-align:center;transition:transform .3s ease,box-shadow .3s ease}@media(max-width: 1200px){.group-card{flex:1 1 calc(33.33% - 20px);max-width:calc(33.33% - 20px)}}@media(max-width: 768px){.group-card{flex:1 1 calc(50% - 20px);max-width:calc(50% - 20px)}}@media(max-width: 480px){.group-card{flex:1 1 100%;max-width:100%}}.group-card:hover{transform:translateY(-5px);box-shadow:0 5px 15px rgba(0,0,0,.3)}.group-card-image{width:100px;height:100px;border-radius:50%;margin-bottom:15px}.group-card-name{font-size:1.5rem;font-weight:700;color:#000;margin-bottom:10px}.group-card-role{font-size:1.1rem;color:#868e96;margin-bottom:10px}.group-card-description{font-size:1rem;color:#000;margin-bottom:10px}.group-card-social-links{display:flex;justify-content:center;gap:10px}.group-card-social-links a{color:#000;font-size:1.5rem;transition:color .3s ease}.group-card-social-links a:hover{color:#8c43ce}.dark-mode .group-category-title{color:#fff}.dark-mode .group-category-title::after{background-color:#8c43ce}.dark-mode .group-card{background-color:#171c28;color:#fff;box-shadow:0 0 10px rgba(255,255,255,.3)}.dark-mode .group-card:hover{transform:translateY(-5px);box-shadow:0 5px 20px rgba(255,255,255,.5)}.dark-mode .group-card .group-card-name{color:#fff}.dark-mode .group-card .group-card-role{color:#fff}.dark-mode .group-card .group-card-description{color:#fff}.dark-mode .group-card .group-card-social-links a{color:#fff}.dark-mode .group-card .group-card-social-links a:hover{color:#8c43ce}